.rightBar{
    flex:3.5;
}

.rightBarWrapper{
    padding: 20px 20px 0 0;
}
.birthdayContainer{
    display: flex;
    align-items: center;
}
.birthdayImg{
width:40px;
height: 40px;
margin-right: 10px;
}
.birthdayText{
    font-weight: 300;
    font-size: 15px;
}
.rightbarAd{
width:100%;
border-radius: 10px;
margin: 30px 0;
}
.rightbarTitle{
    margin-bottom: 20px;
}
.rightbarFriendList{
    padding:0;
    margin:0;
    list-style: none;
}

.rightbarFollowButton{
margin-top: 30px;
margin-bottom: 10px;
border:none;
background-color: #1872f2;
color:white;
border-radius: 5px;
padding: 5px 10px;
display: flex;
align-items: center;
font-size: 16px;
font-weight: 500;
cursor:pointer;
}
.rightbarFollowButton:focus{
outline: none;
}
.rightbarTitle{
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 10px;
}
.rightbarInfo{
margin-bottom:30px;
}
.rightbarInfoItem{
    margin-bottom:10px;  
}
.rightbarInfoKey{
    font-weight: 500;
    color:#555;
    margin-right: 15px;
}
.rightbarInfovalue{
    font-weight: 300;
}
.rightbarFollowingImg{
    width:100px;
    height:100px;
    border-radius: 5px;
    object-fit: cover;
    }
.rightbarFollowings{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.rightbarfollowing{
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    cursor: pointer;
}