.chatOnlineFriend{
    display: flex;
    align-items: center;
    font-weight: 500;
    cursor: pointer;
    margin-top: 10px;
}
.chatOnlineImgContainer{
    margin-right: 10px;
    position: relative;
}
.chatOnlineImg{
    width:32px;
    height:32px;
    border-radius: 50%;
    object-fit: cover;
    border: 2px solid white;
      

}
.chatOnlineBadge{
    width:10px;
    height:10px;
    border-radius: 50%;
    background-color: limegreen;   
    position: absolute;
    top:2px;
    right:2px;
}